import "./scss/principal.scss";

import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import { connect } from "react-redux";
import { fetchSongs } from "../actions";

import SongComponent from "./songs/SongComponent";

class Principal extends React.Component {
  componentDidMount() {
    this.props.fetchSongs();
  }

  renderList = () => {
    const listSongs = this.props.songs.sort((a, b) => {
      return new Date(b.fecha) - new Date(a.fecha);
    });

    let newList;
    const ancho = window.innerWidth;
    if (ancho > 1279) {
      newList = listSongs.slice(0, 12);
    } else if (ancho > 767) {
      newList = listSongs.slice(0, 10);
    } else if (ancho > 566) {
      newList = listSongs.slice(0, 12);
    } else if (ancho > 400) {
      newList = listSongs.slice(0, 8);
    } else {
      newList = listSongs.slice(0, 6);
    }

    return newList.map(song => {
      return <SongComponent key={song.id} {...song} page="principal" />;
    });
  };

  render() {
    return (
      <React.Fragment>
        <Container fluid={false} className="principal-main">
          <Row>
            <Col>
              <h2>
                EL ARTE DE CREAR
                <br />
                MÚSICA ES NUESTRA
                <br />
                PASIÓN
              </h2>
            </Col>
          </Row>
          <Row className="mt-5">
            <Col sm={12}>
              <h6>
                <span>SONIC DEN es un estudio de producción</span>
                <br />
                <span>musical y grabación con más de</span>
                <br />
                <span>20 años de experiencia en el ámbito.</span>
              </h6>
            </Col>
          </Row>
        </Container>
        <Container fluid={true} className="principal-portfolio">
          <Row>
            <Container fluid={false}>
              <Row className="mb-3">
                <Col>
                  <h3>PORTAFOLIO</h3>
                  <p>Más de 100 canciones respaldan nuestra pasión</p>
                </Col>
              </Row>
              <section className="portfolio">{this.renderList()}</section>
            </Container>
          </Row>
        </Container>
        <Container fluid={true} className="footer">
          <Row>
            <Col>
              <footer>RECORDING STUDIO</footer>
              <aside className="footer-social">
                <div>
                  <i className="fab fa-facebook-f"></i>
                </div>
                <div className="mx-1">
                  <i className="fab fa-twitter"></i>
                </div>
                <div>
                  <i className="fab fa-instagram"></i>
                </div>
              </aside>
            </Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

const mapStateToPros = state => {
  return { songs: Object.values(state.song) };
};

export default connect(mapStateToPros, { fetchSongs })(Principal);
