import "./scss/header.scss";

import React from "react";
import { Navbar, Nav } from "react-bootstrap";
import { Link } from "react-router-dom";

import { connect } from "react-redux";
import { changeBackground } from "../actions";

import logo from "../assets/img/logo.png";

class Header extends React.Component {
  onPaginaPrincipal = () => {
    this.props.changeBackground("index");
  };

  onPaginaAcerca = () => {
    this.props.changeBackground("about");
  };

  onPaginaPortfolio = () => {
    this.props.changeBackground("portfolio");
  };

  onPaginaEquipment = () => {
    this.props.changeBackground("equipment");
  };

  onPaginaContact = () => {
    this.props.changeBackground("contact");
  };

  render() {
    return (
      <Navbar bg="dark" variant="dark" expand="lg" sticky="top">
        <Link
          to="/"
          className="ml-3 navbar-brand"
          onClick={this.onPaginaPrincipal}
        >
          <img
            src={logo}
            width="150"
            className="d-inline-block align-top"
            alt="Sonic Den logo"
          />
          <h1 className="d-none">sonic den</h1>
        </Link>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="">
            <Link
              className="nav-link"
              to="/about"
              onClick={this.onPaginaAcerca}
            >
              Acerca de
            </Link>

            <Link
              className="nav-link"
              to="/portfolio"
              onClick={this.onPaginaPortfolio}
            >
              Portafolio
            </Link>

            <Link
              className="nav-link"
              to="/equipment"
              onClick={this.onPaginaEquipment}
            >
              Equipos
            </Link>

            <Link
              className="nav-link"
              to="/contact-us"
              onClick={this.onPaginaContact}
            >
              Contacto
            </Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    );
  }
}

export default connect(null, { changeBackground })(Header);
