import { CHANGE_BACKGROUND } from "../actions/types";

const INITIAL_STATE = {
  imagen: "/static/media/f01x.87fbb631.jpg"
};

export default (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case CHANGE_BACKGROUND:
      return { ...state, imagen: action.payload };

    default:
      return state;
  }
};
